"use client";

import { useState, useRef, useEffect } from "react";
import Link from "next/link";
import {
  AppBar,
  Toolbar,
  Container,
  Box,
  IconButton,
  Button,
  Menu,
  MenuItem,
  Typography,
  Drawer,
  List,
  ListItem,
  ListItemButton,
  ListItemText,
  Divider,
  Paper,
  Avatar,
  useMediaQuery,
  useTheme,
  Badge,
  Stack,
  ClickAwayListener,
  Grow,
  Popper,
} from "@mui/material";
import {
  Menu as MenuIcon,
  Close as CloseIcon,
  Language as LanguageIcon,
  Person as PersonIcon,
  ExpandMore as ExpandMoreIcon,
} from "@mui/icons-material";

// Helper function for FlagCDN API
const getFlagUrl = (countryCode, size = 40) => {
  return `https://flagcdn.com/w${size}/${countryCode.toLowerCase()}.png`;
};

// Languages using FlagCDN API
const languages = [
  {
    code: "US",
    label: "English",
    flag: getFlagUrl("us", 40),
  },
  {
    code: "AE",
    label: "Arabic",
    flag: getFlagUrl("ae", 40),
  },
  {
    code: "TR",
    label: "Turkish",
    flag: getFlagUrl("tr", 40),
  },
  {
    code: "RU",
    label: "Russian",
    flag: getFlagUrl("ru", 40),
  },
  {
    code: "FR",
    label: "French",
    flag: getFlagUrl("fr", 40),
  },
  {
    code: "DE",
    label: "German",
    flag: getFlagUrl("de", 40),
  },
];

const navLinks = [
  { label: "Flights", href: "/flights" },
  { label: "Hotels", href: "/hotels" },
  { label: "Tours", href: "/tours" },
  { label: "Cars", href: "/cars" },
  { label: "Visa", href: "/visa" },
  { label: "Blog", href: "/blog" },
];

// Currencies using same FlagCDN API
const currencies = [
  {
    code: "USD",
    country: "United States",
    flag: getFlagUrl("us", 40),
    symbol: "$",
    name: "US Dollar",
  },
  {
    code: "GBP",
    country: "United Kingdom",
    flag: getFlagUrl("gb", 40),
    symbol: "£",
    name: "British Pound",
  },
  {
    code: "SAR",
    country: "Saudi Arabia",
    flag: getFlagUrl("sa", 40),
    symbol: "ر.س",
    name: "Saudi Riyal",
  },
  {
    code: "EUR",
    country: "Germany",
    flag: getFlagUrl("de", 40),
    symbol: "€",
    name: "Euro",
  },
  {
    code: "PHP",
    country: "Philippines",
    flag: getFlagUrl("ph", 40),
    symbol: "₱",
    name: "Philippine Peso",
  },
];

// Small top strip with contact / quick links
const TopBar = () => {
  return (
    <Box sx={{ backgroundColor: "#f5f7fa", px: { xs: 2, md: 6 }, py: 0.5 }}>
      <Container
        maxWidth="xl"
        sx={{
          display: "flex",
          alignItems: "center",
          justifyContent: "space-between",
        }}
      >
        <Box
          sx={{
            display: "flex",
            gap: 2,
            alignItems: "center",
            fontSize: "0.875rem",
            color: "grey.700",
          }}
        >
      
        </Box>
        <Box
          sx={{
            display: "flex",
            gap: 0,
            alignItems: "center",
            fontSize: "0.875rem",
          }}
        >
          <Link href="/agents/login">Agents</Link>
          <Link href="/customer/login">Customer</Link>
        </Box>
      </Container>
    </Box>
  );
};

const agentLinks = [
  { name: "Login", path: "/agents/login" },
  { name: "Signup", path: "/agents/signup" },
];

const customerLinks = [
  { name: "Login", path: "/customer/login" },
  { name: "Signup", path: "/customer/signup" },
];

// Custom Dropdown Component for Language
const LanguageDropdown = ({
  languages,
  open,
  anchorEl,
  onClose,
  onOpen,
  selected,
  onSelect,
}) => {
  return (
    <>
      <Button
        variant="outlined"
        size="small"
        onClick={onOpen}
        sx={{
          borderRadius: 20,
          borderColor: "grey.300",
          color: "grey.700",
          textTransform: "none",
          fontSize: "0.75rem",
          fontWeight: 500,
          "&:hover": {
            borderColor: "#28a8e2",
            backgroundColor: "white",
          },
          minWidth: "auto",
          px: 1.5,
          py: 0.5,
        }}
      >
        <Box
          component="img"
          src={selected.flag}
          alt={selected.label}
          sx={{
            width: 16,
            height: 12,
            mr: 1,
            borderRadius: 0.5,
          }}
        />
        {selected.label}
        <ExpandMoreIcon sx={{ fontSize: 16, ml: 0.5 }} />
      </Button>

      <Menu
        anchorEl={anchorEl}
        open={open}
        onClose={onClose}
        PaperProps={{
          sx: {
            mt: 1,
            minWidth: 160,
            boxShadow: "0 4px 6px -1px rgb(0 0 0 / 0.1)",
            border: "1px solid",
            borderColor: "grey.200",
            borderRadius: 2,
          },
        }}
      >
        {languages.map((lang, i) => (
          <MenuItem
            key={i}
            onClick={() => {
              onSelect(lang);
              onClose();
            }}
            sx={{
              py: 1,
              fontSize: "0.75rem",
              borderBottom: i < languages.length - 1 ? "1px solid" : "none",
              borderColor: "grey.100",
              "&:hover": {
                backgroundColor: "grey.100",
              },
            }}
          >
            <Box
              component="img"
              src={lang.flag}
              alt={lang.label}
              sx={{
                width: 20,
                height: 15,
                mr: 1.5,
                borderRadius: 0.5,
              }}
            />
            {lang.label}
          </MenuItem>
        ))}
      </Menu>
    </>
  );
};

// Custom Dropdown Component for Currency
const CurrencyDropdown = ({
  currencies,
  open,
  anchorEl,
  onClose,
  onOpen,
  selected,
  onSelect,
}) => {
  return (
    <>
      <Button
        variant="outlined"
        size="small"
        onClick={onOpen}
        sx={{
          borderRadius: 20,
          borderColor: "grey.300",
          color: "grey.700",
          textTransform: "none",
          fontSize: "0.75rem",
          fontWeight: 500,
          "&:hover": {
            borderColor: "#28a8e2",
            backgroundColor: "white",
          },
          minWidth: "auto",
          px: 1.5,
          py: 0.5,
        }}
      >
        <Box
          component="img"
          src={selected.flag}
          alt={selected.country}
          sx={{
            width: 16,
            height: 12,
            mr: 1,
            borderRadius: 0.5,
          }}
        />
        {selected.code}
        <ExpandMoreIcon sx={{ fontSize: 16, ml: 0.5 }} />
      </Button>

      <Menu
        anchorEl={anchorEl}
        open={open}
        onClose={onClose}
        PaperProps={{
          sx: {
            mt: 1,
            minWidth: 180,
            boxShadow: "0 4px 6px -1px rgb(0 0 0 / 0.1)",
            border: "1px solid",
            borderColor: "grey.200",
            borderRadius: 2,
          },
        }}
      >
        {currencies.map((currency, i) => (
          <MenuItem
            key={i}
            onClick={() => {
              onSelect(currency);
              onClose();
            }}
            sx={{
              py: 1,
              fontSize: "0.75rem",
              borderBottom: i < currencies.length - 1 ? "1px solid" : "none",
              borderColor: "grey.100",
              "&:hover": {
                backgroundColor: "grey.100",
              },
            }}
          >
            <Box
              component="img"
              src={currency.flag}
              alt={currency.country}
              sx={{
                width: 20,
                height: 15,
                mr: 1.5,
                borderRadius: 0.5,
              }}
            />
            <Box>
              <Typography variant="body2" fontWeight={500}>
                {currency.code}
              </Typography>
              <Typography variant="caption" color="grey.500">
                {currency.country}
              </Typography>
            </Box>
          </MenuItem>
        ))}
      </Menu>
    </>
  );
};

// Custom Dropdown Component for Agents/Customer
const UserDropdown = ({
  title,
  items,
  open,
  anchorEl,
  onClose,
  onOpen,
  icon,
}) => {
  return (
    <>
      <Button
        variant="outlined"
        size="small"
        onClick={onOpen}
        startIcon={icon}
        sx={{
          borderRadius: 20,
          borderColor: "grey.300",
          color: "grey.700",
          textTransform: "none",
          fontSize: "0.75rem",
          fontWeight: 500,
          "&:hover": {
            borderColor: "#28a8e2",
            backgroundColor: "white",
          },
          minWidth: "auto",
          px: 1.5,
          py: 0.5,
        }}
      >
        {title}
        <ExpandMoreIcon sx={{ fontSize: 16, ml: 0.5 }} />
      </Button>

      <Menu
        anchorEl={anchorEl}
        open={open}
        onClose={onClose}
        PaperProps={{
          sx: {
            mt: 1,
            minWidth: 140,
            boxShadow: "0 4px 6px -1px rgb(0 0 0 / 0.1)",
            border: "1px solid",
            borderColor: "grey.200",
            borderRadius: 2,
          },
        }}
      >
        {items.map((item, i) => (
          <MenuItem
            key={i}
            component={Link}
            href={item.path}
            onClick={onClose}
            sx={{
              py: 1,
              fontSize: "0.75rem",
              borderBottom: i < items.length - 1 ? "1px solid" : "none",
              borderColor: "grey.100",
              "&:hover": {
                backgroundColor: "grey.100",
              },
            }}
          >
            {item.name}
          </MenuItem>
        ))}
      </Menu>
    </>
  );
};

const Header = () => {
  const theme = useTheme();
  const isMobile = useMediaQuery(theme.breakpoints.down("lg"));

  // State for mobile drawer
  const [drawerOpen, setDrawerOpen] = useState(false);

  // State for dropdowns
  const [langAnchorEl, setLangAnchorEl] = useState(null);
  const [currencyAnchorEl, setCurrencyAnchorEl] = useState(null);
  const [agentsAnchorEl, setAgentsAnchorEl] = useState(null);
  const [customerAnchorEl, setCustomerAnchorEl] = useState(null);

  // Selected items
  const [selectedLanguage, setSelectedLanguage] = useState(languages[0]);
  const [selectedCurrency, setSelectedCurrency] = useState(currencies[0]);

  // Handle dropdown opens
  const handleLangOpen = (event) => {
    setLangAnchorEl(event.currentTarget);
    // Close other dropdowns
    setCurrencyAnchorEl(null);
    setAgentsAnchorEl(null);
    setCustomerAnchorEl(null);
  };

  const handleCurrencyOpen = (event) => {
    setCurrencyAnchorEl(event.currentTarget);
    setLangAnchorEl(null);
    setAgentsAnchorEl(null);
    setCustomerAnchorEl(null);
  };

  const handleAgentsOpen = (event) => {
    setAgentsAnchorEl(event.currentTarget);
    setLangAnchorEl(null);
    setCurrencyAnchorEl(null);
    setCustomerAnchorEl(null);
  };

  const handleCustomerOpen = (event) => {
    setCustomerAnchorEl(event.currentTarget);
    setLangAnchorEl(null);
    setCurrencyAnchorEl(null);
    setAgentsAnchorEl(null);
  };

  // Handle dropdown closes
  const handleClose = () => {
    setLangAnchorEl(null);
    setCurrencyAnchorEl(null);
    setAgentsAnchorEl(null);
    setCustomerAnchorEl(null);
  };

  return (
    <>
      <AppBar
        position="sticky"
        sx={{
          backgroundColor: "white",
          color: "grey.900",
          boxShadow: "0 1px 3px 0 rgb(0 0 0 / 0.1)",
          borderBottom: "1px solid",
          borderColor: "grey.200",
        }}
      >
        <Container maxWidth="xl" sx={{ px: { xs:30, md: 14 ,  display: "flex",
                  alignItems: "center",
                  justifyContent: "space-around" } }}>
          <Toolbar disableGutters sx={{ minHeight: { xs: 100, md: 64 } }}>
            {/* Left: Logo */}
            <Box sx={{ display: "flex", alignItems: "center" }}>
              <Box
                component={Link}
                href="/"
                sx={{
                
                  textDecoration: "none",
                }}
              >
                <Box
                  component="img"
                  src="logo.png"
                  alt="Logo"
                  sx={{ width: 100, height: "auto" }}
                />
              </Box>
            </Box>

            {/* Center: Navigation (centred on large screens) */}
            <Box
              sx={{
              
                display: { xs: "none", lg: "flex" },
                justifyContent: "center",
                alignItems: "center",
              }}
            >
              <Box sx={{ }}>
                {navLinks.map((link, i) => (
                  <Button
                    key={i}
                    component={Link}
                    href={link.href}
                    sx={{
                      color: "grey.600",
                      fontSize: "12px",
                      margin:"0px",
              
                      textTransform: "none",
                  
                      borderBottom: "3px solid transparent",
                      borderRadius: 0,
                      "&:hover": {
                        color: "#28a8e2",
                        borderBottom: "3px solid",
                        borderColor: "#28a8e2",
                        backgroundColor: "transparent",
                   
                      },
                      
                    }}
                  >
                    {link.label}
                  </Button>
                ))}
              </Box>
            </Box>

            {/* Right Section - Desktop */}
            <Box
              sx={{
                display: { xs: "none", lg: "flex" },
                alignItems: "center",
                gap: 1,
                fontSize:"8px",
                paddingLeft:"40px"  
              }}
            >
              {/* Language Dropdown */}
              <Box sx={{ position: "relative" }}>
                <LanguageDropdown
                  languages={languages}
                  open={Boolean(langAnchorEl)}
                  anchorEl={langAnchorEl}
                  onClose={handleClose}
                  onOpen={handleLangOpen}
                  selected={selectedLanguage}
                  onSelect={setSelectedLanguage}
                />
              </Box>

              {/* Currency Dropdown */}
              <Box sx={{ position: "relative"}}>
                <CurrencyDropdown
                  currencies={currencies}
                  open={Boolean(currencyAnchorEl)}
                  anchorEl={currencyAnchorEl}
                  onClose={handleClose}
                  onOpen={handleCurrencyOpen}
                  selected={selectedCurrency}
                  onSelect={setSelectedCurrency}
                />
              </Box>

              {/* Agents Dropdown */}
              <Box sx={{ position: "relative" }}>
                <UserDropdown
                  title="Agents"
                  items={agentLinks}
                  open={Boolean(agentsAnchorEl)}
                  anchorEl={agentsAnchorEl}
                  onClose={handleClose}
                  onOpen={handleAgentsOpen}
                  icon={<PersonIcon sx={{ fontSize: 16 }} />}
                />
              </Box>

              {/* Customer Dropdown */}
              <Box sx={{ position: "relative" }}>
                <UserDropdown
                  title="Customer"
                  items={customerLinks}
                  open={Boolean(customerAnchorEl)}
                  anchorEl={customerAnchorEl}
                  onClose={handleClose}
                  onOpen={handleCustomerOpen}
                  icon={<PersonIcon sx={{ fontSize: 16 }} />}
                />
              </Box>
            </Box>

            {/* Mobile Menu Button */}
            <IconButton
              sx={{ display: { lg: "none" }, color: "grey.700" }}
              onClick={() => setDrawerOpen(true)}
            >
              <MenuIcon />
            </IconButton>
          </Toolbar>
        </Container>
      </AppBar>

      {/* Mobile Drawer */}
      <Drawer
        anchor="right"
        open={drawerOpen}
        onClose={() => setDrawerOpen(false)}
        PaperProps={{
          sx: {
            width: 280,
            backgroundColor: "white",
          },
        }}
      >
        <Box sx={{ p: 2 }}>
          {/* Drawer Header */}
          <Box
            sx={{
              display: "flex",
              justifyContent: "space-between",
              alignItems: "center",
              mb: 2,
            }}
          >
            <Typography variant="h6" fontWeight={600}>
              Menu
            </Typography>
            <IconButton onClick={() => setDrawerOpen(false)}>
              <CloseIcon />
            </IconButton>
          </Box>

          <Divider sx={{ mb: 2 }} />

          {/* Navigation Links in Grid */}
          <Box
            sx={{
              display: "grid",
              gridTemplateColumns: "repeat(2, 1fr)",
              gap: 1,
              mb: 3,
            }}
          >
            {navLinks.map((link, i) => (
              <Button
                key={i}
                component={Link}
                href={link.href}
                onClick={() => setDrawerOpen(false)}
                variant="outlined"
                size="small"
                sx={{
                  color: "grey.700",
                  borderColor: "grey.200",
                  borderRadius: 20,
                  textTransform: "none",
                  fontSize: "0.75rem",
                  fontWeight: 500,
                  "&:hover": {
                    borderColor: "#28a8e2",
                    color: "#28a8e2",
                    backgroundColor: "grey.50",
                  },
                }}
              >
                {link.label}
              </Button>
            ))}
          </Box>

          <Divider sx={{ mb: 3 }} />

          {/* Mobile Dropdowns */}
          <Stack spacing={2}>
            {/* Language Select */}
            <Box>
              <Typography
                variant="caption"
                fontWeight={500}
                sx={{ mb: 1, display: "block", color: "grey.600" }}
              >
                Language
              </Typography>
              <SelectMenu
                items={languages}
                selected={selectedLanguage}
                onSelect={setSelectedLanguage}
                renderItem={(item) => (
                  <>
                    <Box
                      component="img"
                      src={item.flag}
                      alt={item.label}
                      sx={{
                        width: 20,
                        height: 15,
                        mr: 2,
                        borderRadius: 0.5,
                      }}
                    />
                    {item.label}
                  </>
                )}
              />
            </Box>

            {/* Currency Select */}
            <Box>
              <Typography
                variant="caption"
                fontWeight={500}
                sx={{ mb: 1, display: "block", color: "grey.600" }}
              >
                Currency
              </Typography>
              <SelectMenu
                items={currencies}
                selected={selectedCurrency}
                onSelect={setSelectedCurrency}
                renderItem={(currency) => (
                  <>
                    <Box
                      component="img"
                      src={currency.flag}
                      alt={currency.country}
                      sx={{
                        width: 20,
                        height: 15,
                        mr: 2,
                        borderRadius: 0.5,
                      }}
                    />
                    <Box>
                      <Typography variant="body2" fontWeight={500}>
                        {currency.code}
                      </Typography>
                      <Typography variant="caption" color="grey.500">
                        {currency.country}
                      </Typography>
                    </Box>
                  </>
                )}
              />
            </Box>

            {/* Agents Links */}
            <Box>
              <Typography
                variant="caption"
                fontWeight={500}
                sx={{ mb: 1, display: "block", color: "grey.600" }}
              >
                Agents
              </Typography>
              <Stack spacing={0.5}>
                {agentLinks.map((item, i) => (
                  <Button
                    key={i}
                    component={Link}
                    href={item.path}
                    onClick={() => setDrawerOpen(false)}
                    startIcon={<PersonIcon sx={{ fontSize: 16 }} />}
                    sx={{
                      justifyContent: "flex-start",
                      color: "grey.700",
                      textTransform: "none",
                      fontSize: "0.75rem",
                      fontWeight: 400,
                      "&:hover": {
                        backgroundColor: "grey.100",
                      },
                    }}
                  >
                    {item.name}
                  </Button>
                ))}
              </Stack>
            </Box>

            {/* Customer Links */}
            <Box>
              <Typography
                variant="caption"
                fontWeight={500}
                sx={{ mb: 1, display: "block", color: "grey.600" }}
              >
                Customer
              </Typography>
              <Stack spacing={0.5}>
                {customerLinks.map((item, i) => (
                  <Button
                    key={i}
                    component={Link}
                    href={item.path}
                    onClick={() => setDrawerOpen(false)}
                    startIcon={<PersonIcon sx={{ fontSize: 16 }} />}
                    sx={{
                      justifyContent: "flex-start",
                      color: "grey.700",
                      textTransform: "none",
                      fontSize: "0.75rem",
                      fontWeight: 400,
                      "&:hover": {
                        backgroundColor: "grey.100",
                      },
                    }}
                  >
                    {item.name}
                  </Button>
                ))}
              </Stack>
            </Box>
          </Stack>
        </Box>
      </Drawer>
    </>
  );
};

// Reusable Select Component for Mobile
const SelectMenu = ({ items, selected, onSelect, renderItem }) => {
  const [open, setOpen] = useState(false);
  const anchorRef = useRef(null);

  return (
    <>
      <Button
        ref={anchorRef}
        variant="outlined"
        size="small"
        fullWidth
        onClick={() => setOpen(!open)}
        sx={{
          borderRadius: 20,
          borderColor: "grey.300",
          color: "grey.700",
          textTransform: "none",
          fontSize: "0.75rem",
          fontWeight: 400,
          justifyContent: "space-between",
          "&:hover": {
            borderColor: "grey.400",
            backgroundColor: "white",
          },
        }}
      >
        <Box sx={{ display: "flex", alignItems: "center" }}>
          {renderItem(selected)}
        </Box>
        <ExpandMoreIcon sx={{ fontSize: 16 }} />
      </Button>

      <Menu
        anchorEl={anchorRef.current}
        open={open}
        onClose={() => setOpen(false)}
        PaperProps={{
          sx: {
            width: anchorRef.current?.clientWidth,
            maxHeight: 200,
            mt: 1,
            boxShadow: "0 4px 6px -1px rgb(0 0 0 / 0.1)",
            border: "1px solid",
            borderColor: "grey.200",
            borderRadius: 2,
          },
        }}
      >
        {items.map((item, i) => (
          <MenuItem
            key={i}
            onClick={() => {
              onSelect(item);
              setOpen(false);
            }}
            sx={{
              py: 1,
              fontSize: "0.75rem",
              borderBottom: i < items.length - 1 ? "1px solid" : "none",
              borderColor: "grey.100",
              "&:hover": {
                backgroundColor: "grey.100",
              },
            }}
          >
            <Box sx={{ display: "flex", alignItems: "center" }}>
              {renderItem(item)}
            </Box>
          </MenuItem>
        ))}
      </Menu>
    </>
  );
};

export default Header;
