import React from "react";
import Link from "next/link";
import {
  Box,
  Container,
  Grid,
  Typography,
  TextField,
  Button,
  Divider,
  IconButton,
  Stack,
  Link as MuiLink,
  List,
  ListItem,
} from "@mui/material";
import {
  Facebook,
  Twitter,
  YouTube,
  Instagram,
} from "@mui/icons-material";
import LinkedInIcon from "@mui/icons-material/LinkedIn";

const Footer = () => {
  return (
    <Box
      component="footer"
      sx={{
        borderTop: 1,
        borderColor: "divider",
        color: "text.primary",
        px: { xs: 3, md: 12 },
       pt: 6,
        fontSize: "8px",
      }}
    >
      <Container maxWidth="lg" >
        {/* Top section: 4 columns grid */}
        <Grid container spacing={20} sx={{ mb: 4}}>
          {/* Column 1 - About Us */}
          <Grid item xs={55} sm={6} md={3}>
            <List dense disablePadding>
              <ListItem disablePadding sx={{ mb: 1 }}>
                <MuiLink
                  component={Link}
                  href="/privacy-policy"
                  color="inherit"
                  underline="hover"
                  variant="body2"
                >
                  About Us
                </MuiLink>
              </ListItem>
              <ListItem disablePadding sx={{ mb: 1 }}>
                <MuiLink
                  component={Link}
                  href="/privacy-policy"
                  color="inherit"
                  underline="hover"
                  variant="body2"
                >
                  Privacy Policy
                </MuiLink>
              </ListItem>
              <ListItem disablePadding>
                <MuiLink
                  component={Link}
                  href="/file-claim"
                  color="inherit"
                  underline="hover"
                  variant="body2"
                >
                  File A Claim
                </MuiLink>
              </ListItem>
            </List>
          </Grid>

          {/* Column 2 - Contact Us */}
          <Grid item xs={12} sm={6} md={3}>
            <List dense disablePadding>
              <ListItem disablePadding sx={{ mb: 1 }}>
                <MuiLink
                  component={Link}
                  href="/become-supplier"
                  color="inherit"
                  underline="hover"
                  variant="body2"
                >
                  Contact Us
                </MuiLink>
              </ListItem>
              <ListItem disablePadding sx={{ mb: 1 }}>
                <MuiLink
                  component={Link}
                  href="/become-supplier"
                  color="inherit"
                  underline="hover"
                  variant="body2"
                >
                  Become A Supplier
                </MuiLink>
              </ListItem>
              <ListItem disablePadding>
                <MuiLink
                  component={Link}
                  href="/careers"
                  color="inherit"
                  underline="hover"
                  variant="body2"
                >
                  Careers And Jobs
                </MuiLink>
              </ListItem>
            </List>
          </Grid>

          {/* Column 3 - Terms Of Use */}
          <Grid item xs={12} sm={6} md={3}>
            <List dense disablePadding>
              <ListItem disablePadding sx={{ mb: 1 }}>
                <MuiLink
                  component={Link}
                  href="/faq"
                  color="inherit"
                  underline="hover"
                  variant="body2"
                >
                  Terms Of Use
                </MuiLink>
              </ListItem>
              <ListItem disablePadding sx={{ mb: 1 }}>
                <MuiLink
                  component={Link}
                  href="/faq"
                  color="inherit"
                  underline="hover"
                  variant="body2"
                >
                  FAQ
                </MuiLink>
              </ListItem>
              <ListItem disablePadding>
                <MuiLink
                  component={Link}
                  href="/how-to-book"
                  color="inherit"
                  underline="hover"
                  variant="body2"
                >
                  How To Book
                </MuiLink>
              </ListItem>
            </List>
          </Grid>

          {/* Column 4 */}
          <Grid item xs={12} sm={6} md={3}>
            <List dense disablePadding>
              <ListItem disablePadding sx={{ mb: 1 }}>
                <MuiLink
                  component={Link}
                  href="/cookies-policy"
                  color="inherit"
                  underline="hover"
                  variant="body2"
                >
                  Cookies Policy
                </MuiLink>
              </ListItem>
              <ListItem disablePadding>
                <MuiLink
                  component={Link}
                  href="/booking-tips"
                  color="inherit"
                  underline="hover"
                  variant="body2"
                >
                  Booking Tips
                </MuiLink>
              </ListItem>
            </List>
          </Grid>
        </Grid>

        {/* Contact & Newsletter */}
        <Box
          sx={{
            display: "flex",
            flexDirection: { xs: "column", md: "row" },
            justifyContent: "space-between",
            alignItems: { xs: "flex-start", md: "center" },
            gap: 1,
            py: 1,
            borderTop: 1,
            borderColor: "divider",
            
          }}
        >
          {/* Contact Info */}
          <Box sx={{ display: "flex", alignItems: "center", width: { xs: "100%", md: "auto" } }}>
            <Box
              component="img"
              src="/logo without name.png"
              alt="Logo"
              sx={{
                width: 48,
                height: 48,
                objectFit: "contain",
                flexShrink: 0,
              }}
            />
            <Box sx={{ ml: 2 }}>
              <List dense disablePadding>
                <ListItem disablePadding sx={{ mb: 0.5 }}>
                  <Typography variant="body2" color="text.secondary">
                    +123456789
                  </Typography>
                </ListItem>
                <ListItem disablePadding sx={{ mb: 0.5 }}>
                  <MuiLink
                    component={Link}
                    href="/privacy-policy"
                    color="text.primary"
                    underline="hover"
                    variant="body2"
                    sx={{ fontWeight: 600 }}
                  >
                    email@agency.com
                  </MuiLink>
                </ListItem>
                <ListItem disablePadding>
                  <MuiLink
                    component={Link}
                    href="/file-claim"
                    color="text.primary"
                    underline="hover"
                    variant="body2"
                    sx={{ fontWeight: 600 }}
                  >
                    Contact Us
                  </MuiLink>
                </ListItem>
              </List>
            </Box>
          </Box>

          {/* Newsletter Signup */}
          <Box sx={{ width: { xs: "100%", md: "auto" } }}>
            <Stack
              direction={{ xs: "column", sm: "row" }}
              spacing={2}
              sx={{ width: "100%" }}
            >
              <TextField
                placeholder="Name"
                variant="outlined"
                size="small"
                sx={{
                
                  "& .MuiOutlinedInput-root": {
                    backgroundColor: "white",
                  },
                }}
              />
              <TextField
                type="email"
                placeholder="Email"
                variant="outlined"
                size="small"
                sx={{
                  flex: 1,
                  "& .MuiOutlinedInput-root": {
                    backgroundColor: "white",
                  },
                }}
              />
              <Button
                variant="contained"
                color="primary"
                sx={{
                 fontSize: "10px",
                 fontWeight: 600,
                  px: 4,
                  whiteSpace: "nowrap",
                  width: { xs: "100%", sm: "auto" },
                }}
              >
                Signup Newsletter
              </Button>
            </Stack>
          </Box>
        </Box>

        {/* Bottom section */}
        <Divider sx={{  }} />
        <Box
          sx={{
            display: "flex",
            flexDirection: { xs: "column", md: "row" },
            justifyContent: "space-between",
            alignItems: "center",
            gap: 3,
            pt: 2,
          }}
        >
          {/* Copyright - FIXED: Removed align prop with object */}
          <Typography 
            variant="body2" 
            color="text.primary"
            sx={{ 
              textAlign: { xs: "center", md: "left" },
              width: { xs: "100%", md: "auto" }
            }}
          >
            © {new Date().getFullYear()} All Rights Reserved by PHPTRAVELS
          </Typography>

          {/* Powered by */}
          <Box sx={{ display: "flex", alignItems: "center", gap: 1 }}>
            <Typography variant="body2" color="text.secondary">
              Powered by
            </Typography>
            <Box sx={{ display: "flex", alignItems: "center", gap: 1 }}>
              <Box
                component="img"
                src="/logo without name.png"
                alt="PHPTRAVELS"
                sx={{ width: 20, height: 20 }}
              />
              <Typography variant="body2" sx={{ fontWeight: 600 }}>
                PHPTRAVELS
              </Typography>
              <Typography variant="body2" color="text.secondary">
                v3.1
              </Typography>
            </Box>
          </Box>

          {/* Social Icons */}
          <Stack direction="row" spacing={2}>
            <IconButton size="small" color="inherit">
              <Facebook fontSize="small" />
            </IconButton>
            <IconButton size="small" color="inherit">
              <Twitter fontSize="small" />
            </IconButton>
            <IconButton size="small" color="inherit">
              <LinkedInIcon fontSize="small" />
            </IconButton>
            <IconButton size="small" color="inherit">
              <YouTube fontSize="small" />
            </IconButton>
            <IconButton size="small" color="inherit">
              <Instagram fontSize="small" />
            </IconButton>
          </Stack>
        </Box>
      </Container>
    </Box>
  );
};

export default Footer;