"use client";

import { useState } from "react";
import { Box, Container, Typography, Paper } from "@mui/material";
import TravelTabs from "./components/TravelTabs";
import FlightSearchForm from "./components/FlightSearchForm";
import HotelSearchForm from "./components/HotelSearchForm";
import TourSearchForm from "./components/TourSearchForm";
import CarSearchForm from "./components/CarSearchForm";
import VisaSearchForm from "./components/VisaSearchForm";

const HeroSection = () => {
  const [activeTab, setActiveTab] = useState("flights");

  return (
    <Box
      sx={{
        position: "relative",
        minHeight: "65vh",
        bgcolor: "grey.900",
        color: "white",
      }}
    >
      {/* Background Image */}
      <Box
        component="img"
        src="/styles/Her0SectionImg.png"
        alt="Travel Background"
        sx={{
          position: "absolute",
          inset: 0,
          width: "100%",
          height: 360,
          objectFit: "cover",
          objectPosition: "center",
          zIndex: 1,
        }}
      />
      <Box
        sx={{
          position: "absolute",
          inset: 0,
          bgcolor: "rgba(0,0,0,0.6)",
          zIndex: 2,
        }}
      />

      <Container sx={{ position: "relative", zIndex: 3, pt: 10 }}>
        <Box maxWidth="md" mx="auto" textAlign="center" mb={4}>
          <Typography variant="h4" fontWeight="bold" gutterBottom>
            Your Trip Starts Here!
          </Typography>
          <Typography variant="body1" color="grey.400">
            Let us help you plan your next journey — the one that will leave a
            lifetime of memories.
          </Typography>
        </Box>

        {/* Tabs */}
        <TravelTabs activeTab={activeTab} setActiveTab={setActiveTab} />

        {/* Search Form Box */}
        <Paper
          elevation={6}
          sx={{
            position: "relative",
            mt: 3,
            p: 3,
            borderRadius: 3,
            backdropFilter: "blur(10px)",
          }}
        >
          {activeTab === "flights" && <FlightSearchForm />}
          {activeTab === "hotels" && <HotelSearchForm />}
          {activeTab === "tours" && <TourSearchForm />}
          {activeTab === "cars" && <CarSearchForm />}
          {activeTab === "visa" && <VisaSearchForm />}
        </Paper>
      </Container>
    </Box>
  );
};

export default HeroSection;
