"use client";

import React, { useState, useEffect } from 'react';
import {
  Box,
  Grid,
  Button,
  TextField,
  MenuItem,
  Select,
  FormControl,
  InputLabel,
  IconButton,
  Paper,
  InputAdornment,
  Typography,
  Divider,
} from '@mui/material';
import {
  SwapHoriz as SwapHorizIcon,
  Search as SearchIcon,
  LocationOn as LocationOnIcon,
  CalendarToday as CalendarTodayIcon,
  Person as PersonIcon,
  ExpandMore as ExpandMoreIcon,
} from '@mui/icons-material';

// CORRECT IMPORTS for v6
import { DatePicker } from '@mui/x-date-pickers/DatePicker';
import { AdapterDateFns } from '@mui/x-date-pickers/AdapterDateFns';
import { LocalizationProvider } from '@mui/x-date-pickers/LocalizationProvider';

// OR for v5 compatibility:
// import { DatePicker } from '@mui/lab';
// import { AdapterDateFns } from '@mui/x-date-pickers/AdapterDateFns';
// import { LocalizationProvider } from '@mui/x-date-pickers/LocalizationProvider';

// Airport Data
const countriesAirports = [
  {
    country: "UAE",
    cities: [
      { city: "Dubai", airport: "Dubai International Airport", code: "DXB" },
      { city: "Abu Dhabi", airport: "Abu Dhabi Intl Airport", code: "AUH" },
    ]
  },
  {
    country: "Pakistan",
    cities: [
      { city: "Karachi", airport: "Jinnah International Airport", code: "KHI" },
      { city: "Lahore", airport: "Allama Iqbal Airport", code: "LHE" }
    ]
  },
  {
    country: "India",
    cities: [
      { city: "Delhi", airport: "Indira Gandhi International Airport", code: "DEL" },
      { city: "Mumbai", airport: "Chhatrapati Shivaji Maharaj Airport", code: "BOM" },
    ]
  },
  {
    country: "Saudi Arabia",
    cities: [
      { city: "Riyadh", airport: "King Khalid Intl Airport", code: "RUH" },
      { city: "Jeddah", airport: "King Abdulaziz Intl Airport", code: "JED" },
    ]
  },
  {
    country: "Germany",
    cities: [
      { city: "Berlin", airport: "Berlin Brandenburg Airport", code: "BER" },
      { city: "Frankfurt", airport: "Frankfurt Airport", code: "FRA" },
    ]
  },
  {
    country: "Canada",
    cities: [
      { city: "Toronto", airport: "Toronto Pearson Airport", code: "YYZ" },
      { city: "Vancouver", airport: "Vancouver Intl Airport", code: "YVR" },
    ]
  },
  {
    country: "Australia",
    cities: [
      { city: "Sydney", airport: "Sydney Airport", code: "SYD" },
      { city: "Melbourne", airport: "Melbourne Airport", code: "MEL" },
    ]
  }
];

const FlightSearchForm = () => {
  const [flyingFrom, setFlyingFrom] = useState('');
  const [destinationTo, setDestinationTo] = useState('');
  const [selectedTripType, setSelectedTripType] = useState('one-way');
  const [selectedCabinClass, setSelectedCabinClass] = useState('economy');
  const [departDate, setDepartDate] = useState(new Date());
  const [returnDate, setReturnDate] = useState(new Date(Date.now() + 7 * 24 * 60 * 60 * 1000));
  const [travellers, setTravellers] = useState({
    adults: 1,
    children: 0,
    infants: 0,
  });
  const [showTravellerMenu, setShowTravellerMenu] = useState(false);

  const allAirports = countriesAirports.flatMap(country =>
    country.cities.map(city => ({
      city: city.city,
      airport: city.airport,
      code: city.code,
      label: `${city.city} (${city.code})`,
    }))
  );

  const swapLocations = () => {
    const temp = flyingFrom;
    setFlyingFrom(destinationTo);
    setDestinationTo(temp);
  };

  const handleTravellerChange = (type, operation) => {
    setTravellers(prev => ({
      ...prev,
      [type]: operation === 'increase' ? prev[type] + 1 : Math.max(0, prev[type] - 1),
    }));
  };

  const totalTravellers = travellers.adults + travellers.children + travellers.infants;

  return (
    <LocalizationProvider dateAdapter={AdapterDateFns}>
      <Box sx={{ width: '100%' }}>
        {/* Trip type and cabin class */}
        <Grid container spacing={2} sx={{ mb: 2 }}>
          <Grid item xs={12} sm={6} md={3}>
            <FormControl fullWidth size="small">
              <InputLabel>Trip Type</InputLabel>
              <Select
                value={selectedTripType}
                label="Trip Type"
                onChange={(e) => setSelectedTripType(e.target.value)}
              >
                <MenuItem value="one-way">One Way</MenuItem>
                <MenuItem value="return">Return</MenuItem>
              </Select>
            </FormControl>
          </Grid>
          <Grid item xs={12} sm={6} md={3}>
            <FormControl fullWidth size="small">
              <InputLabel>Cabin Class</InputLabel>
              <Select
                value={selectedCabinClass}
                label="Cabin Class"
                onChange={(e) => setSelectedCabinClass(e.target.value)}
              >
                <MenuItem value="economy">Economy</MenuItem>
                <MenuItem value="premium-economy">Premium Economy</MenuItem>
                <MenuItem value="business">Business</MenuItem>
                <MenuItem value="first-class">First Class</MenuItem>
              </Select>
            </FormControl>
          </Grid>
        </Grid>

        {/* Main Search Grid */}
        <Grid container spacing={2} alignItems="center">
          {/* Locations */}
          <Grid item xs={12} md={4}>
            <Grid container spacing={1} alignItems="center">
              <Grid item xs={5}>
                <FormControl fullWidth size="small">
                  <TextField
                    select
                    value={flyingFrom}
                    onChange={(e) => setFlyingFrom(e.target.value)}
                    label="Flying From"
                    InputProps={{
                      startAdornment: (
                        <InputAdornment position="start">
                          <LocationOnIcon fontSize="small" />
                        </InputAdornment>
                      ),
                    }}
                  >
                    {allAirports.map((airport, index) => (
                      <MenuItem key={index} value={airport.label}>
                        <Box>
                          <Typography variant="body2">{airport.city}</Typography>
                          <Typography variant="caption" color="textSecondary">
                            {airport.airport}
                          </Typography>
                        </Box>
                      </MenuItem>
                    ))}
                  </TextField>
                </FormControl>
              </Grid>
              <Grid item xs={2} sx={{ display: 'flex', justifyContent: 'center' }}>
                <IconButton
                  onClick={swapLocations}
                  size="small"
                  sx={{
                    bgcolor: 'primary.light',
                    color: 'primary.main',
                    '&:hover': { bgcolor: 'primary.main', color: 'white' },
                  }}
                >
                  <SwapHorizIcon />
                </IconButton>
              </Grid>
              <Grid item xs={5}>
                <FormControl fullWidth size="small">
                  <TextField
                    select
                    value={destinationTo}
                    onChange={(e) => setDestinationTo(e.target.value)}
                    label="Destination To"
                    InputProps={{
                      startAdornment: (
                        <InputAdornment position="start">
                          <LocationOnIcon fontSize="small" />
                        </InputAdornment>
                      ),
                    }}
                  >
                    {allAirports.map((airport, index) => (
                      <MenuItem key={index} value={airport.label}>
                        <Box>
                          <Typography variant="body2">{airport.city}</Typography>
                          <Typography variant="caption" color="textSecondary">
                            {airport.airport}
                          </Typography>
                        </Box>
                      </MenuItem>
                    ))}
                  </TextField>
                </FormControl>
              </Grid>
            </Grid>
          </Grid>

          {/* Dates - FIXED DatePicker usage */}
          <Grid item xs={12} md={3}>
            <Grid container spacing={1}>
              <Grid item xs={selectedTripType === 'return' ? 6 : 12}>
                <DatePicker
                  label="Depart Date"
                  value={departDate}
                  onChange={(newValue) => setDepartDate(newValue)}
                  slotProps={{
                    textField: {
                      size: 'small',
                      fullWidth: true,
                      InputProps: {
                        startAdornment: (
                          <InputAdornment position="start">
                            <CalendarTodayIcon fontSize="small" />
                          </InputAdornment>
                        ),
                      },
                    },
                  }}
                />
              </Grid>
              {selectedTripType === 'return' && (
                <Grid item xs={6}>
                  <DatePicker
                    label="Return Date"
                    value={returnDate}
                    onChange={(newValue) => setReturnDate(newValue)}
                    slotProps={{
                      textField: {
                        size: 'small',
                        fullWidth: true,
                        InputProps: {
                          startAdornment: (
                            <InputAdornment position="start">
                              <CalendarTodayIcon fontSize="small" />
                            </InputAdornment>
                          ),
                        },
                      },
                    }}
                  />
                </Grid>
              )}
            </Grid>
          </Grid>

          {/* Travellers */}
          <Grid item xs={12} md={3}>
            <FormControl fullWidth>
              <TextField
                size="small"
                value={`${totalTravellers} Traveller${totalTravellers !== 1 ? 's' : ''}`}
                onClick={() => setShowTravellerMenu(!showTravellerMenu)}
                InputProps={{
                  startAdornment: (
                    <InputAdornment position="start">
                      <PersonIcon fontSize="small" />
                    </InputAdornment>
                  ),
                  endAdornment: (
                    <InputAdornment position="end">
                      <ExpandMoreIcon fontSize="small" />
                    </InputAdornment>
                  ),
                  readOnly: true,
                }}
              />
              {showTravellerMenu && (
                <Paper
                  elevation={3}
                  sx={{
                    position: 'absolute',
                    top: '100%',
                    left: 0,
                    right: 0,
                    mt: 1,
                    zIndex: 10,
                    p: 2,
                  }}
                >
                  <Box sx={{ mb: 2 }}>
                    <Typography variant="subtitle2" gutterBottom>
                      Adults (12+)
                    </Typography>
                    <Box display="flex" alignItems="center" justifyContent="space-between">
                      <IconButton
                        size="small"
                        onClick={() => handleTravellerChange('adults', 'decrease')}
                        disabled={travellers.adults <= 1}
                      >
                        -
                      </IconButton>
                      <Typography>{travellers.adults}</Typography>
                      <IconButton
                        size="small"
                        onClick={() => handleTravellerChange('adults', 'increase')}
                      >
                        +
                      </IconButton>
                    </Box>
                  </Box>
                  
                  <Box sx={{ mb: 2 }}>
                    <Typography variant="subtitle2" gutterBottom>
                      Children (2-11)
                    </Typography>
                    <Box display="flex" alignItems="center" justifyContent="space-between">
                      <IconButton
                        size="small"
                        onClick={() => handleTravellerChange('children', 'decrease')}
                        disabled={travellers.children <= 0}
                      >
                        -
                      </IconButton>
                      <Typography>{travellers.children}</Typography>
                      <IconButton
                        size="small"
                        onClick={() => handleTravellerChange('children', 'increase')}
                      >
                        +
                      </IconButton>
                    </Box>
                  </Box>
                  
                  <Box>
                    <Typography variant="subtitle2" gutterBottom>
                      Infants (0-2)
                    </Typography>
                    <Box display="flex" alignItems="center" justifyContent="space-between">
                      <IconButton
                        size="small"
                        onClick={() => handleTravellerChange('infants', 'decrease')}
                        disabled={travellers.infants <= 0}
                      >
                        -
                      </IconButton>
                      <Typography>{travellers.infants}</Typography>
                      <IconButton
                        size="small"
                        onClick={() => handleTravellerChange('infants', 'increase')}
                      >
                        +
                      </IconButton>
                    </Box>
                  </Box>
                </Paper>
              )}
            </FormControl>
          </Grid>

          {/* Search Button */}
          <Grid item xs={12} md={2}>
            <Button
              variant="contained"
              color="primary"
              fullWidth
              size="large"
              startIcon={<SearchIcon />}
              sx={{
                height: '40px',
                boxShadow: 3,
                '&:hover': {
                  boxShadow: 6,
                },
              }}
            >
              Search
            </Button>
          </Grid>
        </Grid>
      </Box>
    </LocalizationProvider>
  );
};

export default FlightSearchForm;